<script>
  export default {
    //   这个东西是为了方便调试
    name: 'ele_section_form',
    data() {
      return {
        labelPosition: 'right',
        //  这是表单绑定的数据模型。。还给组件起了一个名字 ref="dynamicValidateForm"
        dynamicValidateForm: {
          template_id:'请输入模板ID',
          template_description:'请输入模板描述',
          crawl:'爬取方式',
          crawldate:Date.now(),
          target_url:'爬取的目标网址',
          params:'爬取参数',
          //  网页解析规则
          Web_page_parsing_rules:[
            {
              expression:'表达式',
              date:new Date().getTime(),
              Field_name:'字段名'
            },
            {
              expression:'表达式',
              date:new Date().getTime(),
              Field_name:'字段名'
            },
            {
              expression:'表达式',
              date:new Date().getTime(),
              Field_name:'字段名'
            }
          ]
        },
        dynamicValidateFormdata: {
          template_id:'模板ID',
          template_description:'模板描述',
          crawl:'爬取方式',
          crawldate:"爬取时间",
          target_url:'爬取网址',
          params:'爬取参数'
        },
        rules: {
          template_id: [
            { required: true, message: '请输入模板ID', trigger: 'blur' },
            { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          template_description: [
            { required: true, message: '请输入模板描述', trigger: 'blur' },
            { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          target_url: [
            { required: true, message: '请输入爬取的目标地址', trigger: 'blur' },
          ],
          params: [
            { required: false, message: '请输入爬取参数', trigger: 'blur' },
          ],
          crawldate: [
            { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
          ],
          crawl: [
            { required: true, message: '请选择请求方式', trigger: 'blur' },
            { min: 3, max: 4, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ] ,
          field_name: [
            { required: true, message: '请选择请求方式', trigger: 'blur' },
            { min: 0, max:10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.dynamicValidateForm);
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      remove_Field_name(item) {
        var index = this.dynamicValidateForm.Web_page_parsing_rules.indexOf(item)
        if (index !== -1) {
          this.dynamicValidateForm.Web_page_parsing_rules.splice(index, 1)
        }
      },
      add_Field_name() {
        this.dynamicValidateForm.Web_page_parsing_rules.push({
          expression:'表达式',
          date:Date.now(),
          Field_name:'字段名'
        });
      },
      addWeb_page_parsing_rules() {
        this.dynamicValidateForm.Web_page_parsing_rules.push({
          value: '',
          key: Date.now()
        });
      }

    },
    computed:{

    }
    ,
    components: {
      // home
    }
  }
</script>
<!-- 样式代码 -->
<style>

</style>
<!-- 展示模板App.vue是程序的主入口 -->
<template>
  <!--  导航栏  -->
  <div style="padding-top: 30px;">
    <el-form :rules="rules" :model="dynamicValidateForm" ref="dynamicValidateForms" label-width="100px" class="demo-dynamic" :label-position="labelPosition">
      <el-form-item :label="dynamicValidateFormdata.template_id" prop="template_id">
        <el-input v-model="dynamicValidateForm.template_id"></el-input>
      </el-form-item>
      <el-form-item :label="dynamicValidateFormdata.template_description" prop="template_description">
        <el-input v-model="dynamicValidateForm.template_description"></el-input>
      </el-form-item>
      <el-form-item :label="dynamicValidateFormdata.crawl" prop="crawl">
        <el-select v-model="dynamicValidateForm.crawl" placeholder="请选择请求方式">
          <el-option label="GET" value="get"></el-option>
          <el-option label="POST" value="post"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item :label="dynamicValidateFormdata.crawldate">
          <el-date-picker type="date" placeholder="选择日期" v-model="dynamicValidateForm.crawldate" style="width: 100%;"></el-date-picker>
      </el-form-item>
      <el-form-item :label="dynamicValidateFormdata.target_url" prop="target_url">
        <el-input v-model="dynamicValidateForm.target_url"></el-input>
      </el-form-item>
      <el-form-item :label="dynamicValidateFormdata.params" prop="params">
        <el-input v-model="dynamicValidateForm.params"></el-input>
      </el-form-item>
      <!--<template v-for="(item, index) in dynamicValidateForm.Web_page_parsing_rules">-->
      <el-form-item style="margin-left: -130px;padding-left: 0px;"
                    v-for="(item, index) in dynamicValidateForm.Web_page_parsing_rules"
                    :key="index"
      >

        <el-col :span="9">
          <!--:prop="'Web_page_parsing_rules[' + index + '].Field_name'"-->
          <!--{ min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }-->
            <el-form-item :label="'字段名'+index"
                          :prop="'Web_page_parsing_rules[' + index + '].Field_name'"
                          :rules=" [{ required: true, message: '请输入字段名', trigger: 'blur' } ]"
            >
              <el-input v-model="item.Field_name"
              ></el-input>
            </el-form-item>
        </el-col>
        <!-- prop只是一个验证的名字而已，调试用的  -->
          <el-col :span="9">
            <el-form-item :label="'解析规则'+index"
                          :prop="'Web_page_parsing_rules[' + index + '].expression'"
                          :rules=" [{ required: true, message: '请输入解析表达式', trigger: 'blur' }] "
            >
              <el-input v-model="item.expression"></el-input>
            </el-form-item>
          </el-col>
        <el-col :span="2">
          <el-button @click.prevent="remove_Field_name(item)">删除</el-button>
        </el-col>
      </el-form-item>
      <!--  新增数据  -->
      <el-form-item>
        <el-button type="primary" @click="submitForm('dynamicValidateForms')">提交</el-button>
        <el-button @click="add_Field_name">新增字段</el-button>
        <el-button @click="resetForm('dynamicValidateForms')">重置</el-button>
      </el-form-item>
      <!--</template>-->
    </el-form>
  </div>
</template>



