import type from './type.js'
export default {
  [type.switch_section_currentView]:function (state,obj) {
    console.log("section组件切换");
    state.currentView=obj.currentView;
  } ,
  [type.login]:function (state,obj) {
    console.log("登录："+obj);
    state.userdata=obj;
  }
}



