<script>
  export default {
    //   这个东西是为了方便调试
    name: 'ele_section_mydata_modify_form_data',
    data() {
      return {
        //   图片上传处理
        imageUrl: '',
        dialogImageUrl: '',
        dialogVisible: false,
        currentDate: new Date(),
        mydata:{
          username:"王蒿",
          password:"王蒿",
          autograph:"我的签名",
          mydynamic:"我的动态",
          My_head_img:"../assets/hamburger.50e4091.png",
          My_hobby:"我的爱好",
          My_friend:"我的好友",
          Last_login_time:Date.now()
        },
        rules: {
          username: [
            { type: 'string',required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          password: [
            { type: 'string',required: true, message: '请输入密码', trigger: 'change' }
          ],
          autograph: [
            { type: 'string', required: false, message: '我的签名', trigger: 'change' }
          ],
          mydynamic: [
            { type: 'string', required: false, message: '我的动态', trigger: 'change' }
          ],
          My_head_img: [
            { type: 'string', required: false, message: '我的头像', trigger: 'change' }
          ],
          My_hobby: [
            { required: false, message: '我的爱好', trigger: 'change' }
          ],
          My_friend: [
            { required: false, message: '我的好友', trigger: 'change' }
          ],
          Last_login_time: [
            { type: 'date',required: false, message: '上次登录时间', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      //  修改头像
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;
        // if (!isJPG) {
        //   this.$message.error('上传头像图片只能是 JPG 格式!');
        // }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        console.log("文件地址")
        console.log(file.url)
        // this.imageUrl = URL.createObjectURL(file.raw);
        return isJPG && isLt2M;
      },
      changeUpload: function(file, fileList) {
        this.fileList = fileList;
        this.$nextTick(
          () => {
            let upload_list_li = document.getElementsByClassName('el-upload-list')[0].children;
            for (let i = 0; i < upload_list_li.length; i++) {
              let li_a = upload_list_li[i];
              let imgElement = document.createElement("img");
              imgElement.setAttribute('src', fileList[i].url);
              imgElement.setAttribute('style', "max-width:50%;padding-left:25%");
              if (li_a.lastElementChild.nodeName !== 'IMG') {
                li_a.appendChild(imgElement);
              }
            }
          });
      },
      handlePreview(file) {
        console.log("预览。。");
        console.log(file);
        this.dialogImageUrl = file.url;
        console.log("地址");
        console.log(file.url);
        this.dialogVisible = true;
      },
      //  修改信息
      handleSubmit2(arge) {
        var _this = this;
        console.log("这里跳转到"+arge);
        this.loadingtext="拼命加载中-2秒后跳转到"+arge;
        _this.loading2=true;
        console.log(arge);
        window.setTimeout(function(){
          // _this.loading2=_this.loading2?false:true;
          _this.$router.push(arge);
        }, 2000);
        console.log();
        // this.$refs.ruleForm2.validate((valid) => {
        //   if (valid) {
        //     //_this.$router.replace('/table');
        //     this.logining = true;
        //     //NProgress.start();
        //     var loginParams = { username: this.ruleForm2.username, password: this.ruleForm2.password };
        //     requestLogin(loginParams).then(data => {
        //       this.logining = false;
        //       //NProgress.done();
        //       let { msg, code, user } = data;
        //       if (code !== 200) {
        //         this.$message({
        //           message: msg,
        //           type: 'error'
        //         });
        //       } else {
        //         sessionStorage.setItem('user', JSON.stringify(user));
        //         this.$router.push({ path: '/table' });
        //       }
        //     });
        //   } else {
        //     console.log('error submit!!');
        //     return false;
        //   }
        // });
      }
    },
    computed:{

    }
    ,
    components: {
      // home
    }
  }
</script>
<!-- 样式代码 -->
<style scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .image {
    width: 100%;
    border-radius: 50%;
    display: block;
  }

</style>
<!-- 展示模板App.vue是程序的主入口 -->
<template>
  <!--  导航栏  -->
  <div style="padding-top: 30px;">
    <!--:rules="rules"  -->
    <el-form :rules="rules" :model="mydata"  ref="mydataform" label-width="120px" class="demo-ruleForm">
      <el-form-item style="margin-left: -120px">
        <el-col :span="11">
          <el-form-item prop="username" label="用户名">
            <el-input v-model="mydata.username"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="3">
          <span style="margin-left: 60px;">修改头像:</span>
          </el-col>
        <el-col :span="10">
          <el-popover
            ref="popover4"
            placement="right"
            width="400"
            trigger="click">
            <el-upload
              class="avatar-uploader"
              action=""
              list-type="picture-card"
              :before-upload="beforeAvatarUpload"
              :on-preview="handlePreview"
              :show-file-list="true"
              :auto-upload='false'
              :limit="1"
            >
              <!--:on-change='changeUpload'-->
              <img v-if="imageUrl" :src="imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
          </el-popover>
          <!--:on-success="handleAvatarSuccess"-->
      <img  id="imgs"  v-if="!dialogVisible" src="../assets/hamburger.50e4091.png"  v-popover:popover4 style=" width:40px;" class="image">
      <img  id="imgs2" v-if="dialogVisible" :src="dialogImageUrl"  v-popover:popover4 style=" width:40px;" class="image">

        </el-col>
      </el-form-item>
      <el-form-item  prop="password" label="密码">
        <el-input type="password" v-model="mydata.password"></el-input>
      </el-form-item>
      <el-form-item prop="autograph" label="我的签名">
        <el-input v-model="mydata.autograph"></el-input>
      </el-form-item>
      <el-form-item prop="mydynamic" label="我的动态">
        <el-input v-model="mydata.mydynamic"></el-input>
      </el-form-item>
      <el-form-item prop="My_hobby" label="我的爱好">
        <el-input v-model="mydata.My_hobby"></el-input>
      </el-form-item>
      <el-form-item prop="My_friend" label="我的好友">
        <el-input v-model="mydata.My_friend"></el-input>
      </el-form-item>
      <el-form-item prop="Last_login_time" label="上次登录时间">
        <el-input v-model="mydata.Last_login_time"></el-input>
      </el-form-item>
      <el-form-item style="width:100%;">
        <!-- 注册成功应该弹出一个页面框，并且跳转。  -->
        <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2('/home')">修改信息</el-button>
        <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
      </el-form-item>

      <!--autograph:"我的签名",-->
      <!--mydynamic:"我的动态",-->
      <!--My_head_img:"../assets/hamburger.50e4091.png",-->
      <!--My_hobby:"我的爱好",-->
      <!--My_friend:"我的好友",-->
      <!--Last_login_time:Date.now()-->

    </el-form>
  </div>
</template>



