import axios from 'axios';
import routes from "../router/routes";
import type from "../vuex/type";
//   这是数据接口
let base = 'http://127.0.0.1:8088';
//  https://segmentfault.com/a/1190000008610059
// 使用这个东西，进行数据的修改，，看懂这个文档就可以了
// https://github.com/hzzly/xyy-vue/
//https://cnodejs.org/topic/5993e5acf36051a45246c07a  VUE可视化布局。。
let API={
  //   登录
  login:function (user,context,resdata,typevalue){
    var fm = new FormData();
    fm.append('userName', "dddddddddd");
    fm.append('img', "sssssssss");
    // axios.post(`${base}/upload`, fm,{
    return  axios.post(`${base}/uploadjsonbodyparse`, {sss:"ddddd"},{
      //  这里是设置文件上传类型
      //https://cnodejs.org/topic/54929c5561491ead0cc7bff2  json接收
      //https://www.cnblogs.com/wushifeng/p/6707248.html   内容类型
      // headers:{'Content-Type':'multipart/form-data'}
      headers:{'Content-Type':'application/json;charset=utf-8'}
    })
      .then(function (response) {
        console.log(response);
        resdata=response.data;
        context.commit(typevalue,resdata);
        return response.data;
      })
      .catch(function (error) {
        console.log(error);
      });
  }

}
export default API;



