import Vue from 'vue'
import Vuex from 'vuex'
//  为什么是*  ，因为那是多个方法
//  全局变量，被所有组件使用
import  state from './state.js'
//  计算属性(依赖属性)
import  getters from './getters.js'
//  数据修改事务
import  mutations from './mutations.js'
// 数据修改异步事务
import  actions from './actions.js'
Vue.use(Vuex)
// 应用初始状态
// 定义所需的 mutations..事务
// 创建 store 实例
export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
    // store 的计算属性,让多个组件用不同的方式使用里面的属性。其实就是函数
})



