<!-- 展示模板App.vue是程序的主入口 -->
<template>
  <div>
    <!--   头部 -->
    <header>
      <ele_header></ele_header>
    </header>
    <!--   侧边栏导航 -->
    <nav style="float: left;width: 10%;">
      <ele_nav></ele_nav>
    </nav>
    <section style="float: left;width: 90%;">
      <!--position:absolute;left:60px;-->
      <!--<keep-alive>-->
      <!-- 这里动态切换组件，不使用路由 -->
      <component v-bind:is="currentView">
      </component>
      <!--  现在我们使用嵌套路由来做切换 -->
      <!--<h2>home {{ $route.params.id }}-&#45;&#45;&#45;&#45;&#45;&#45;&#45;&#45;{{$store.state.currentView}}</h2>-->
      <!--<router-view>       </router-view>-->
      <!--<router-view  :name="$store.state.currentView"></router-view>-->
      <!--</keep-alive>-->
    </section>
    <!--<article>article</article>-->
    <!--<aside>aside</aside>-->
    <footer>xxxxxxddddddddddxxxxxxx</footer>

    <!--我是APP-->
    <!--<el-button>123123</el-button>-->
    <!--<home></home>-->
  </div>
</template>
<script>
  // 导入组件..只有导入了才能使用。。但是怎么结合路由呢？。。这里面可以用this.store啊和router啊。
  import home from '../views/Home.vue'
  import ele_nav from '../components/ele_nav.vue'
  import ele_header from '../components/ele_header'
  import ele_section_img from '../components/ele_section_img'
  import ele_section_table from '../components/ele_section_table'
  import ele_section_form from '../components/ele_section_form'
  import ele_section_template from '../components/ele_section_template'
  import ele_section_mydata_form from '../components/ele_section_mydata_form'
  import ele_section_mydata_modify_form_data from '../components/ele_section_mydata_modify_form_data'
  import ele_section_search from '../components/ele_section_search'
  import type from '../vuex/type.js'
  export default {
    //   这个东西是为了方便调试
    name: 'home',
    data() {
      return {
        sections:["ele_section_img","ele_section_table","ele_section_form","ele_section_mydata_form"],
      };
    },
    computed:{
      currentView:function () {
        return this.$store.state.currentView;
      }
    },
    methods:{
      changecurrentView_m:function (obj) {
        console.log("事务同步提交-视图切换app.vue："+obj);
        this.$store.commit(type.switch_section_currentView,obj);
      },
      changecurrentView_a:function (obj) {
        console.log("事务异步提交-视图切换app.vue："+obj);
        this.$store.dispatch('a_changecurrentView',obj);
      }
    },
    components: {
      "home":home,
      "ele_nav":ele_nav,
      "ele_section_img":ele_section_img,
      "ele_section_table":ele_section_table,
      "ele_section_form":ele_section_form,
      "ele_section_template":ele_section_template,
      "ele_section_mydata_form":ele_section_mydata_form,
      "ele_section_mydata_modify_form_data":ele_section_mydata_modify_form_data,
      "ele_section_search":ele_section_search,
      "ele_header":ele_header
    }
  }
</script>
<!-- 样式代码 -->
<style scoped>
  .el-row {
    margin-bottom: 20px;
  &:last-child {
     margin-bottom: 0;
   }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>




