<template>
  <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container"
           v-loading="loading2"
           :element-loading-text="loadingtext"
           element-loading-spinner="el-icon-loading"
  >
    <!--element-loading-background="rgba(0, 0, 0, 0.8)"-->
    <h3 class="title">用户注册</h3>
    <el-form-item prop="username">
      <el-input type="text" v-model="ruleForm2.username" auto-complete="off" placeholder="用户名"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input type="password" v-model="ruleForm2.password" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <!--  记住密码的话应该把密码存储到本地存储  -->
    <!--  http://www.cnblogs.com/lonelyGentleman/p/6730607.html  -->
    <!--  localStorage 把vuex的数据存储到，这里，这样很方便  -->
    <!--  后台进行api.js调用，类似于写自己的控制器  -->
    <!--  用这个东西，还可以用来反爬。。很棒很棒  -->
    <!--   https://zhidao.baidu.com/question/1643412762151516300.html    -->
    <el-checkbox v-model="checked" @change="remember" checked class="remember">记住密码</el-checkbox>
    <el-tooltip class="item" effect="dark" content="点击我登录账号" placement="right-end">
      <el-tag type="success" style="margin-left: 60px;" @click.native.prevent="register('/login')">已有账号!登录?</el-tag>
    </el-tooltip>

    <el-form-item style="width:100%;">
      <!-- 注册成功应该弹出一个页面框，并且跳转。  -->
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2('/home')">注册</el-button>
      <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
    </el-form-item>
  </el-form>
</template>

<script>

  export default {
    name:"register",
    data() {
      return {
        loadingtext:"拼命加载中-2秒后跳转到",
        loading2:false,
        logining: false,
        ruleForm2: {
          username: 'admin',
          password: '123456'
        },
        rules2: {
          username: [
            { required: true, message: '请输入账号', trigger: 'blur' },
            //{ validator: validaePass }
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            //{ validator: validaePass2 }
          ]
        },
        checked: true
      };
    },
    methods: {

      handleReset2() {
        this.$refs.ruleForm2.resetFields();
      },
      register(obj){
        console.log("登录账号-跳转登录页面");
        var _this=this;
        this.loading2=true;
        this.loadingtext="拼命加载中-2秒后跳转到"+obj;
        window.setTimeout(function(){
          // _this.loading2=_this.loading2?false:true;
          // _this.$router.push(arge);
          _this.loading2=false;
          _this.$router.push(obj);
        }, 2000);
        // this.route
        // this.$router.push(obj);
      },
      remember() {
        console.log("记住密码--这里应该调用东西进行测试，，获取后端的数据接口");
        console.log(this.checked);
      },
      handleSubmit2(arge) {
        var _this = this;
        console.log("这里跳转到"+arge);
        this.loadingtext="拼命加载中-2秒后跳转到"+arge;
        _this.loading2=true;
        console.log(arge);
        window.setTimeout(function(){
          // _this.loading2=_this.loading2?false:true;
          _this.$router.push(arge);
        }, 2000);
        console.log();
        // this.$refs.ruleForm2.validate((valid) => {
        //   if (valid) {
        //     //_this.$router.replace('/table');
        //     this.logining = true;
        //     //NProgress.start();
        //     var loginParams = { username: this.ruleForm2.username, password: this.ruleForm2.password };
        //     requestLogin(loginParams).then(data => {
        //       this.logining = false;
        //       //NProgress.done();
        //       let { msg, code, user } = data;
        //       if (code !== 200) {
        //         this.$message({
        //           message: msg,
        //           type: 'error'
        //         });
        //       } else {
        //         sessionStorage.setItem('user', JSON.stringify(user));
        //         this.$router.push({ path: '/table' });
        //       }
        //     });
        //   } else {
        //     console.log('error submit!!');
        //     return false;
        //   }
        // });
      }
    }
  }

</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
      /*color: palegreen;*/
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>



