<template>

  <div>
    <!--这里用了过渡效果 fade是vue的过渡样式 -->
    <!--out-in是动画效果 -->
    <!--https://www.zhihu.com/question/38213423  这里是vue的应用-->
    <!--过渡模式   这里只用了过渡模式-->
    <transition name="fade"
                mode="out-in">
      <router-view></router-view>
    </transition>

  </div>
</template>

<script>
  export default {
    name: 'app',
    components: {
    }
  }

</script>

<style>
  body {
      margin: 0px;
      padding: 0px;
      /*background: url(assets/bg1.jpg) center !important;
            background-size: cover;*/
      // background: #1F2D3D;
      font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
      font-size: 14px;
      -webkit-font-smoothing: antialiased;
    }
  /*  这是进入的过渡*/
  .fade-enter-active,
  .fade-leave-active {
    transition: all .2s ease;
  }

  .fade-enter,
  .fade-leave-active {
    opacity: 0;
  }
</style>



