<script>
  import type from '../vuex/type.js'
  export default {
    //   这个东西是为了方便调试
    name: 'ele_header',
    data() {
      return {
        activeIndex: '1',
        activeIndex2: '1'
      };
    },
    computed:{
      ele_header_data:function () {
        console.log(this.$store.state.ele_header_data);
        return this.$store.state.ele_header_data;
      }
    },
    methods: {
      //  此处调用方法进行数视图切换。。action
      handleSelect(key, keyPath) {
        // console.log("key:"+key);
        // console.log("keyPath:"+keyPath);
        console.log(keyPath);
        var obj={ currentView:"ele_section_img"};
        switch (keyPath[0]){
          //   快爬虫
          case "ele_header_data.Fast_reptilian.name":
            // 执行代码块 1
            obj.currentView="ele_section_img";
            break;
          //   我的工作台
          case "ele_header_data.My_workbench.name":
            // obj.currentView="ele_section_table";
                    switch(keyPath[1])
                    {
                      // 模板生成
                      case "My_workbench0":
                        // 执行代码块 1
                        obj.currentView="ele_section_form";
                        break;
                      //  数据分享
                      case "My_workbench1":
                        // 执行代码块 2
                        obj.currentView="ele_section_table";
                        break;
                      //  搜一搜
                      case "My_workbench2":
                        obj.currentView="ele_section_search";
                        break;
                      default:
                        //搜一搜
                        obj.currentView="ele_section_search";
                    }
            break;
          //   我的数据
          case "ele_header_data.My_data.name":
                    switch(keyPath[1])
                    {
                      //我的模板
                      case "My_data0":
                        // 执行代码块 1
                        obj.currentView="ele_section_template";
                        break;
                      //  历史数据
                      case "My_data1":
                        // 执行代码块 2
                        obj.currentView="ele_section_table";
                        break;;
                      default:
                        // 我的模板
                        obj.currentView="ele_section_table";
                    }
            break;
          //        我的信息
          case "ele_header_data.My_message.name":
            obj.currentView="ele_section_mydata_form";
            switch(keyPath[1])
            {
              //查看我的信息
              case "My_message0":
                // 执行代码块 1
                obj.currentView="ele_section_mydata_form";
                break;
              //  修改我的信息
              case "My_message1":
                // 执行代码块 2
                obj.currentView="ele_section_mydata_modify_form_data";
                break;;
              default:
                // 我的模板
                obj.currentView="ele_section_table";
            }
            break;
          default:
            //   快爬虫
            obj.currentView="ele_section_img";
            // 与 case 1 和 case 2 不同时执行的代码
        };
        //  此处进行事务提交
        //  切换路由
        console.log("路由切换："+"/home/"+obj.currentView);
        // this.$router.push("/home/"+obj.currentView);
        // this.$router.push("/home/ele_section_table");
        // this.$router.push({ path: `/home/${obj.currentView}` });
        this.changecurrentView_a(obj);

      },
      changecurrentView_m:function (obj) {
        console.log("事务同步提交-视图切换app.vue："+obj);
        this.$store.commit(type.switch_section_currentView,obj);
      },
      changecurrentView_a:function (obj) {
        console.log("事务异步提交-视图切换app.vue："+obj);
        this.$store.dispatch('a_changecurrentView',obj);
      }
    }
    ,
    components: {
      // home
    }
  }
</script>
<!-- 样式代码 -->
<style>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
</style>
<!-- 展示模板App.vue是程序的主入口 -->
<template>
  <!--  导航栏  -->
 <div>
   <el-menu
     :default-active="activeIndex2"
     class="el-menu-demo"
     mode="horizontal"
     @select="handleSelect"
     background-color="#545c64"
     text-color="#fff"
     active-text-color="#ffd04b">
     <!--菜单栏-->
     <el-menu-item index="ele_header_data.Fast_reptilian.name"  class="animated infinite bounce">{{ele_header_data.Fast_reptilian.name}}</el-menu-item>
     <!--  子菜单  -->
     <el-submenu index="ele_header_data.My_workbench.name">
       <template slot="title">{{ele_header_data.My_workbench.name}}</template>
      <el-menu-item  v-for="(item, index) in ele_header_data.My_workbench.list"  :index="'My_workbench'+index">{{item.name}}
       </el-menu-item>
     </el-submenu>
     <el-submenu index="ele_header_data.My_data.name">
       <template slot="title">{{ele_header_data.My_data.name}}</template>
       <el-menu-item  v-for="(item, index) in ele_header_data.My_data.list"  :index="'My_data'+index">{{item.name}}
       </el-menu-item>
     </el-submenu>
     <el-submenu index="ele_header_data.My_message.name">
       <template slot="title">{{ele_header_data.My_message.name}}</template>
       <el-menu-item  v-for="(item, index) in ele_header_data.My_message.list"  :index="'My_message'+index">{{item.name}}
       </el-menu-item>
     </el-submenu>

   </el-menu>
 </div>
</template>



