//test  这是异步提交数据
import type from './type'
import API from '../api/api.js'
import mutations from "./mutations";
export default {
    //  异步切换视图这里应该是还要异步请求数据的
  a_changecurrentView:function (context,obj) {
      //obj  { currentView:"xxxxxx"}
      console.log("异步切换视图:"+type.switch_section_currentView+"-->"+obj.currentView);
      context.commit(type.switch_section_currentView,obj);
    },
  a_login:function (context,user) {
      //obj  { currentView:"xxxxxx"}
      console.log("action异步登录:"+type.login+"-->"+user);
      var resdata={};
      var data=API.login(user,context,resdata,type.login);
      console.log(user+"返回的数据-------action--:"+data+"  ------"+resdata);

    }
}



